
@media only screen and (max-width: 796px) {
  .responsive__heading{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 50px 0px;
  }
  .responsive__myprojects{
    position: absolute;
    top: 1050px;
  }
  .responsive__maintext{
    width: 60%;
  }
}

@media only screen and (min-width: 796px) {
  .responsive__heading{
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 50px 0px;
  }
  .responsive__myprojects{
    position: absolute;
    top: 960px
  }
  .responsive__maintext{
    width: 40%;
  }
}



#main {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #080808;
    color: white;
    
}

.my-section{

    font-size: x-large !important;
    font-weight: bold !important;
    color: #40e0d0;
    margin-top: 3rem;
}

.section0 {
  opacity: 0;
  animation-name: load;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.section1{
    opacity: 0;
    animation-name: load;
    animation-delay: 3s;
    animation-duration: 1s;
    animation-fill-mode: forwards;
}

.horizontalline{
    width: 90vw;
    background-color: #0c0032;
    color: #35d300 !important;
}

.roadway {
    stroke-dasharray: calc(45vw + 22rem);
    animation: dash 10s linear infinite;
  }

.roadway__delayed {
    stroke-dasharray: calc(45vw + 22rem);
    animation: dash 5s linear infinite;
  }
  
  @keyframes dash {
    to {
      stroke-dashoffset: calc(90vw + 44rem);
    }
  }

  @keyframes load {
    to {
      opacity: 1;
    }
  }

  #button1{
    box-shadow:0px 0px 0px 1px #d38600 inset !important;
    color: #40e0d0 !important;
  }

#button2{
  box-shadow:0px 0px 0px 1px #3500d3 inset;
  color: #40e0d0;
  text-align: center;
  border-radius: 5px;
  padding: 9px 0;
}





